@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');
body{
    font-family: 'VT323', sans-serif;
    font-weight: 500;
    background: linear-gradient(to right, #13021c, #07112a);
}

li{
    @apply px-4;
    @apply cursor-pointer;
}

.content-div {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 250px;
    border-radius: 0;
    border: 3px solid #521b8a;
}

.content-div:hover {
    background-image: linear-gradient(
        to bottom,
        rgba(134, 0, 236, 0.67),
        hsla(273.826, 96%, 70%, 1)  
    )!important;
}

input[type=checkbox]{
    accent-color:  #ab0b53;
}

.skill{
    list-style: none;
    padding: 0;
    margin: 0;
    text-transform: uppercase;
    margin-top: 5%;
    padding: 20px;

}

.skill li{
    margin: 20px 0;
    padding: 10px;
}

.skill h1{
    text-align: center;
}
.skill h3{
    margin: 5px
}

.bar{
    background-color: #353b48;
    display: block;
    height: 20px;
    border-radius: 10px;
    overflow: hidden;
    transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
}

.bar span{
    height: 20px;
    float: left;
    background: linear-gradient(135deg, rgb(134, 0, 236)0%, rgb(187, 103, 252)100%)
}

.javascript{
    width: 100%;
}

.unity{
    width: 100%;
}

.react{
    width: 92%;
}

.unity-vr{
    width: 88%;
}

.next-js{
    width: 56%;
}

.photon{
    width: 70%;
}

.rest{
    width: 66%;
}

.firebase{
    width: 50%;
}
.comment{
    color: rgb(158, 78, 219);
}